@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;display:grid;grid-template-rows:70px 1fr}.Header{background-color:#f0a26d;width:100%;color:#fff}.Main{display:grid;grid-template-columns:repeat(4, 22vw);grid-template-rows:repeat(2, 18vw);grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:15px;row-gap:15px;background-color:#232121;height:100%;padding:20px 50px}.Clock{grid-column-start:1;grid-column-end:1;grid-row-start:1;grid-row-end:2;height:100%;width:100%;background-color:#f5f0ea;border-radius:20px}.ReadyList{grid-column-start:1;grid-column-end:1;grid-row-start:2;grid-row-end:-1;height:100%;width:100%;background-color:#24262b;border-radius:20px;overflow-y:scroll}.Serving{grid-column-start:2;grid-column-end:4;grid-row-start:1;grid-row-end:1;height:100%;width:100%;background-color:#f5f0ea;border-radius:20px}.Crew{grid-column-start:2;grid-column-end:3;grid-row-start:2;grid-row-end:2;height:100%;width:100%;background-color:#f5f0ea;border-radius:20px}.Taps{grid-column-start:3;grid-column-end:4;grid-row-start:2;grid-row-end:2;height:100%;width:100%;background-color:#f5f0ea;border-radius:20px}.Queue{grid-column-start:4;grid-column-end:-1;grid-row-start:1;grid-row-end:-1;height:100%;width:100%;background-color:#24262b;border-radius:20px;overflow-y:scroll}.panel{font:100% "Satisfy",cursive}.panel h1{color:#394b45}.Order{display:flex;padding:10px;color:white;justify-content:center;justify-content:space-between;flex-wrap:wrap}.highlight{color:#e39c96}.beerimg{width:auto;height:40px}.order-details{align-self:top}.serving{margin-left:10px;display:flex;color:black;flex-wrap:nowrap}#highlight-title{color:#e39c96}.beersimg{width:auto;height:50px;margin-left:50px;margin-bottom:15px}.panel .readyNr{color:#f6c83f}.ReadyList div{display:grid;grid-template-columns:1fr 2fr}.ReadyList div *{justify-self:center;align-self:center}.taps-beer{display:grid;grid-template-columns:repeat(3, 1fr)}

